
Template.__checkName("home");
Template["home"] = new Template("Template.home", (function() {
  var view = this;
  return HTML.DIV({
    "class": "page"
  }, HTML.Raw("\n		<h1>DeeeeeDjay</h1>\n		\n		"), Spacebars.include(view.lookupTemplate("currentGuests")), "\n		", Spacebars.include(view.lookupTemplate("genderRatio")), "\n		", Spacebars.include(view.lookupTemplate("ageRatio")), "\n\n  ");
}));

Template.__checkName("currentGuests");
Template["currentGuests"] = new Template("Template.currentGuests", (function() {
  var view = this;
  return HTML.DIV({
    "class": "current-guests-container"
  }, HTML.Raw("\n		<h2>Total</h2>\n		"), Blaze.View("lookup:getNumberOfGuests", function() {
    return Spacebars.mustache(view.lookup("getNumberOfGuests"));
  }), "\n	");
}));

Template.__checkName("genderRatio");
Template["genderRatio"] = new Template("Template.genderRatio", (function() {
  var view = this;
  return HTML.DIV({
    "class": "gender-ratio-container"
  }, "\n		", HTML.DIV({
    "class": "number-of-women-container"
  }, "\n			", HTML.Raw("<h2>Women</h2>"), "\n			", Blaze.View("lookup:getNumberOfWomen", function() {
    return Spacebars.mustache(view.lookup("getNumberOfWomen"));
  }), "\n		"), "\n		", HTML.DIV({
    "class": "number-of-men-container"
  }, "\n			", HTML.Raw("<h2>Men</h2>"), "\n			", Blaze.View("lookup:getNumberOfMen", function() {
    return Spacebars.mustache(view.lookup("getNumberOfMen"));
  }), "\n		"), "\n	");
}));

Template.__checkName("ageRatio");
Template["ageRatio"] = new Template("Template.ageRatio", (function() {
  var view = this;
  return HTML.DIV({
    "class": "age-ratio-container"
  }, "\n		", HTML.DIV({
    "class": "twenties-age-container"
  }, "\n			", HTML.Raw("<h2>Twenties</h2>"), "\n			", Blaze.View("lookup:getNumberOfTwenties", function() {
    return Spacebars.mustache(view.lookup("getNumberOfTwenties"));
  }), "\n		"), "\n		", HTML.DIV({
    "class": "thirties-age-container"
  }, "\n			", HTML.Raw("<h2>Thirties</h2>"), "\n			", Blaze.View("lookup:getNumberOfThirties", function() {
    return Spacebars.mustache(view.lookup("getNumberOfThirties"));
  }), "\n		"), HTML.Raw('\n		<!-- <div class="fourties-age-container">\n			<h2>Twenties</h2>\n			{{ getNumberOfFourties }}\n		</div>\n		<div class="fifties-age-container">\n			<h2>Twenties</h2>\n			{{ getNumberOfFifties }}\n		</div>\n		<div class="Sixties-age-container">\n			<h2>Twenties</h2>\n			{{ getNumberOfSixties }}\n		</div> -->\n	'));
}));
